.loading-container {
	display: flex;
	height: 100vh;
	align-items: center;
	justify-content: center;
	background: url("./../Conversation/images/whatsapp_wallpaper_2.png");
}
.loader {
	position: relative;
	width: 150px;
	height: 150px;
	border-radius: 50%;
	background: linear-gradient(45deg, transparent 40%, skyblue);
	animation: move 0.8s linear infinite;
}
.loader::before {
	position: absolute;
	content: "";
	top: 6px;
	left: 6px;
	right: 6px;
	bottom: 6px;
	background: #03141f;
	border-radius: 50%;
	z-index: 2;
}
.loader::after {
	position: absolute;
	content: "";
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background: linear-gradient(45deg, transparent 40%, skyblue);
	filter: blur(20px);
}
@keyframes move {
	to {
		transform: rotate(360deg);
		filter: hue-rotate(360deg);
	}
}
